.step-item {
  padding: 8px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.step-item-header {
  display: flex;
  align-items: center;
  gap: 8px;
}

.item-outlined {
  box-shadow: inset 1px 1px 0px #cbc6df, inset -1px -1px 0px #cbc6df;
  border-radius: 4px;
  padding: 24px 32px;
  min-height: 56px;
}

.step-item-name {
  font-weight: 600;
  color: #444444;
}

.step-item-description {
  font-size: 12px;
  color: #393551;
}

.add-workflow-item {
  display: flex;
  align-items: center;
  justify-content: center;
}